@import url('https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap');

/* general */
header{
    background-color: none;
    padding: 3% 2% 3% 2%;
}

body{
    background-color: #f0f0f0;
    padding-left: 27%;
    padding-right: 27%;
    padding-top: 1%;
}

ul{
    list-style-type:disc;
}

li{
    font-family: Merriweather, Georgia, serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.7em;
    color:#333333;
}

/* link boxes */
div.links{
    margin: 3%;
    border: 1px solid lightgrey;
    border-radius: 5px;
    background-color: white;
}

div.links:hover{
    text-decoration: underline;
    border: 1px solid black;
}

div.links img{
    display: block;
    padding-top: 2em;
    padding-bottom: 1em;
    margin-left: auto;
    margin-right: auto;
    width: 3em;
    height: auto;
}

div.linkdesc{
    padding: 0.1em;
    text-align: center;
}

* {
    box-sizing: border-box;
}

.responsive {
    padding: 0 6 px;
    float: left;
    width: 24.99999%;
}

@media screen and (max-width: 1150px) {
    body{
        padding-left: 20%;
        padding-right:20%
    }
}

@media only screen and (max-width:900px){
    .responsive {
        width: 49.99999%;
        margin: 6px 0;
    }
    body{
        padding: 2%;
    }
}

@media only screen and (max-width: 600px){
    .responsive {
        width: 100%;
        margin: 0;
        /*
        padding-left: 30%;
        padding-right: 30%;
        */
    }
    body{
        padding: 2%;
    }
}

.clearfix:after{
    content: "";
    display: table;
    clear:both;
}


