/* fonts */
h1 {
    font-family: -apple-system, BlinkMacSystemFont, Roboto, 'Arial', Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 48px;
    line-height: 1em;
    margin-top: 0;
    margin-bottom: 0;
}

p{
    font-family: Merriweather, Georgia, serif;
    font-weight: 400;
    font-size: 18px;
    text-indent: 10%;
    line-height: 1.7em;
    color:#333333;
}

p.links{
    font-family: -apple-system, BlinkMacSystemFont, Roboto, 'Arial', Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 16px;
    text-indent: 0;
    line-height: 1em;
}

p.nospacing{
    text-indent: 0;
    margin-top: 0;
    margin-bottom: 0;
}

a:link{
    font-family: Merriweather, Georgia, serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.7em;
    color:green;
    text-decoration: none;
}

a:visited{
    color: purple;
}

a:hover{
    text-decoration:underline;
}

a:active{
    text-decoration: underline;
    color:darkgreen;
}